/* martel-200normal - latin */
@font-face {
  font-family: 'Martel';
  font-style: normal;
  font-display: swap;
  font-weight: 200;
  src:
    local('Martel Extra Light '),
    local('Martel-Extra Light'),
    url('./files/martel-latin-200.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/martel-latin-200.woff') format('woff'); /* Modern Browsers */
}

